import React from "react"
import { Button } from "react-bootstrap"
import { Link } from "gatsby"
import ring from "../../images/home/ring.svg"
import arrow from "../../images/home/arrow-up-right.svg"

const CardForm = () => {

const data = [
    {
        icon: ring,
        heading: "Included By Design",
        description:"Explore real-life examples of inclusive design to inspire and inform your own work.",
        buttonText: "Stay up to date",
        linkText: "Submit an example",
        linkIcon: arrow,
    },
]

return (
    <>
        {data.map((e,i) => {
            return(
            <div className="card-form-box" key={i}>
                <img src={e.icon} alt="ring" />
                <h4>{e.heading}</h4>
                <p>{e.description}</p>
                <Button>{e.buttonText}</Button>
                <Link to="/">{e.linkText} <img src={e.linkIcon} alt="arrow" /></Link>
            </div>
            )
        })}
    </>
)
}

export default CardForm